@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  font-family: 'Roboto', sans-serif;
  color: #444857; }

html, body, #root {
  background-color: #f5f5f5;
  font-size: 18px; }

.page {
  background-color: #f5f5f5;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: auto;
  padding: 12px; }
  .page .heading {
    margin: 18px auto;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    display: flex;
    flex-direction: row;
    text-align: center; }
    .page .heading h1, .page .heading h2, .page .heading h3 {
      margin: 0; }
    .page .heading h3 {
      padding-top: 6px;
      font-size: 14px;
      color: #999999; }
    .page .heading img {
      margin: 0 1em;
      height: 70px;
      width: 100px; }
  .page .actionIcons {
    display: flex;
    flex-direction: row;
    justify-content: center; }
    .page .actionIcons .action {
      padding: 6px;
      display: flex;
      flex-direction: row;
      font-size: 14px;
      cursor: pointer; }
      .page .actionIcons .action i, .page .actionIcons .action span {
        color: #999999;
        align-self: center; }
      .page .actionIcons .action:hover i, .page .actionIcons .action:hover span {
        color: #0288D1; }
      .page .actionIcons .action .actionIcon {
        font-size: 18px;
        padding-right: 6px;
        padding-left: 6px; }
  .page .sprintPlanning {
    margin: auto;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content; }
    .page .sprintPlanning input {
      margin: 0 3px;
      border: solid 1px #999999;
      padding: 3px;
      width: 24px;
      height: 16px;
      border-radius: 5px; }
    .page .sprintPlanning input[type="number"]::-webkit-outer-spin-button,
    .page .sprintPlanning input[type="number"]::-webkit-inner-spin-button {
      -webkit-appearance: none;
      margin: 0; }
    .page .sprintPlanning input[type="number"] {
      -moz-appearance: textfield; }
    .page .sprintPlanning .recap {
      margin: 1em;
      border: 1px solid #0288D1;
      border-radius: 5px;
      padding: 1em;
      height: -webkit-fit-content;
      height: -moz-fit-content;
      height: fit-content;
      background-color: #ffffff;
      text-align: center;
      font-weight: bold;
      font-size: 20px; }

@media only screen and (max-width: 640px) {
  .heading img {
    height: 35px !important;
    width: 50px !important; }
  .tableHeader {
    display: none; }
  .tableContent {
    display: flex;
    flex-direction: column !important; } }

.hoursPlanning {
  margin: 1em;
  border: 1px solid #0288D1;
  border-radius: 5px;
  padding: 6px 0;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  background-color: #ffffff; }
  .hoursPlanning .title-end {
    margin: 1em;
    border-bottom: solid 3px #0288D1;
    padding-bottom: 5px;
    display: flex;
    flex-direction: row;
    justify-content: space-between; }
    .hoursPlanning .title-end h3 {
      margin: 0;
      color: #0288D1; }
    .hoursPlanning .title-end .icon {
      padding: 0 5px;
      color: #0288D1;
      font-size: 20px;
      cursor: pointer; }
      .hoursPlanning .title-end .icon :hover {
        color: #FFA726; }
  .hoursPlanning .column {
    display: inline-block;
    flex-direction: row;
    width: 123px;
    padding: 10px;
    height: 20px;
    text-align-last: center;
    overflow: hidden;
    vertical-align: bottom;
    text-overflow: ellipsis; }
  .hoursPlanning .tableHeader .column {
    text-align: center;
    font-size: 14px;
    font-weight: bold;
    padding-left: 10px;
    padding-right: 10px; }
    .hoursPlanning .tableHeader .column:first-child {
      padding-left: 20px;
      margin-left: 30px; }
  .hoursPlanning .tableContent {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    display: flex;
    flex-direction: row;
    margin-left: 30px;
    margin-right: 30px; }
    .hoursPlanning .tableContent .emergency {
      border-left: solid 1px #999999;
      height: auto;
      display: flex;
      align-items: center;
      justify-content: center; }
  .hoursPlanning .add {
    padding: 10px;
    padding-left: 55px;
    position: relative; }
    .hoursPlanning .add .plus > i {
      position: absolute;
      left: 30px;
      bottom: 12px;
      color: #00c700;
      font-size: 20px;
      cursor: pointer; }
    .hoursPlanning .add input {
      margin: 0;
      width: 100px;
      flex: 1 1; }
  .hoursPlanning .total {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: auto; }
    .hoursPlanning .total p {
      font-weight: bold; }
  .hoursPlanning:hover {
    box-shadow: 0 1px 10px #01579B; }

@media only screen and (max-width: 640px) {
  .tableHeader {
    display: none; }
  .tableContent {
    display: flex;
    flex-direction: column !important; } }

.teammate {
  position: relative; }
  .teammate .minus {
    position: absolute;
    left: 0px;
    width: 20px;
    height: 20px;
    display: flex;
    justify-content: center;
    font-size: 20px;
    align-items: flex-end;
    cursor: pointer; }
    .teammate .minus > i {
      color: red; }
  .teammate .column {
    border-left: solid 1px #999999; }
    .teammate .column:first-child {
      border-left: 0;
      padding-left: 20px;
      position: relative;
      text-align-last: left; }
      .teammate .column:first-child .editMode {
        width: 20px;
        height: 20px;
        border-radius: 20px;
        background-color: red;
        display: flex;
        justify-content: center;
        box-shadow: 0 1px 5px #999999;
        cursor: pointer; }
  .teammate .hours4every {
    color: #999999;
    padding-left: 5px;
    font-size: 15px; }
  .teammate:hover {
    background-color: #E1F5FE; }

@media only screen and (max-width: 640px) {
  .teammate {
    display: flex;
    flex-direction: column; } }

